interface Props {
  value: string;
  color: string;
  onClick(letter: string): void;
}

export const Button = ({ value, color, onClick }: Props) => {
  return (
    <button style={{ background: color }} onClick={() => onClick(value)}>
      {value}
    </button>
  );
};

